<template>
  <div class="ordes">
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row>
        <el-col :span="15">
          <el-input
            placeholder=""
            v-model="query.query"
            class="input-with-select"
            clearable
            @clear="clear"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="search"
            ></el-button>
          </el-input>
        </el-col>
      </el-row>
      <el-table :data="orders" border style="width: 100%">
        <el-table-column type="index" label="#"> </el-table-column>
        <el-table-column prop="order_number" label="" width="300">
        </el-table-column>
        <el-table-column prop="order_price" label=""> </el-table-column>
        <el-table-column prop="pay_status" label="">
          <template v-slot="scope">
            <el-tag type="success" v-if="scope.row.pay_status === '1'"
              ></el-tag
            >
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label=""> </el-table-column>
        <el-table-column prop="create_time" label="">
          <template slot-scope="scope">
            {{ scope.row.create_time | moment("YYYY-MM-DD HH:MM:SS") }}
          </template>
        </el-table-column>
        <el-table-column label="">
          <template v-slot="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="edit(scope.row)"
            ></el-button>
            <el-button
              type="success"
              icon="el-icon-location"
              size="mini"
              @click="process(scope.row.user_id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        :page-sizes="[10, 15, 20, 25]"
        :page-size="query.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        @current-change="currentChange"
        @size-change="sizeChange"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="addressVisible" width="50%">
      <el-form
        :model="addressForm"
        :rules="addressFormRules"
        ref="addressFormRef"
        label-width="100px"
      >
        <el-form-item label="/" prop="address1">
          <el-cascader
            :options="cityData"
            v-model="addressForm.address1"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="addressForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addressVisible = false"> </el-button>
        <el-button type="primary" @click="saveEdit"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <Process :dialogVisible.sync="dialogVisible" />
  </div>
</template>

<script>
import Process from "@/components/Process.vue";
import { mapState, mapActions } from "vuex";
import citys from "../assets/citydata";
export default {
  components: { Process },
  data() {
    return {
      query: { query: "", pagenum: 1, pagesize: 10 },
      addressVisible: false,
      addressForm: {
        address1: "",
        address2: "",
      },
      cityData: [],
      addressFormRules: {
        address1: [
          { required: true, message: "", trigger: "blur" },
        ],
        address2: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      dialogVisible: false,
    };
  },
  created() {
    this.getOrders(this.query);
    this.cityData = citys;
  },
  computed: {
    ...mapState({
      orders: (state) => state.orders.orders,
      total: (state) => state.orders.total,
    }),
  },
  methods: {
    // 
    currentChange(value) {
      this.query.pagenum = value;
      this.getOrders(this.query);
    },
    // 
    sizeChange(value) {
      this.query.pagesize = value;
      this.getOrders(this.query);
    },
    // 
    search() {
      this.getOrders(this.query);
    },
    // 
    clear() {
      this.getOrders(this.query);
    },
    // 
    edit(row) {
      this.addressVisible = true;
    },
    // 
    saveEdit() {
      this.$refs.addressFormRef.validate((valida) => {
        if (valida) {
        }
      });
    },
    // 
    process(id) {
      this.dialogVisible = true;
      // this.getPhysics(id);
    },
    ...mapActions(["getOrders", "getPhysics"]),
  },
};
</script>

<style lang="less">
.el-breadcrumb {
  font-size: 12px;
  margin-bottom: 20px;
}
.ordes {
  .el-table {
    font-size: 12px;
    margin-top: 20px;
  }
}
.el-cascader-menu {
  height: 200px;
}
</style>
